import styled from "styled-components";

export const Wrapper = styled.a`
  display: flex;
  text-decoration: none;
  justify-content: center;
  align-items: center;
  padding: 1.3em;
`;

export const Icon = styled.i`
  font-size: 2em;
  color: #fff;
`;



